import styled from 'styled-components'
import { CourseModel } from '../../../models/CourseModel';
import { Draggable, DraggableProvided, DraggableStateSnapshot } from 'react-beautiful-dnd'
import Tooltip from '@material-ui/core/Tooltip';
import { IError } from '../../../interfaces/ModelInterfaces';
import { TermModel } from '../../../models/TermModel';
import { Menu, MenuItemProps, Popup } from 'semantic-ui-react';
import React from 'react';

const getBackgroundColor = (props) => {
  return (
    props.isPrerequisiteOfCurrentDraggable
      ? '#c5eafc'
      : props.isSelected
        ? 'lightcyan'
        : props.hasErrors()
          ? '#f5bfc8'
          : props.isDragging
            ? 'lightgrey'
            : 'white'
  )
}

const Container = styled.div`
  border: 2px solid lightgrey;
  border-radius:8px;
  padding: 8px;
  margin: 0px 2px 8px 2px;
  background-color: ${getBackgroundColor};
  ${(props) =>
    props.isDragging
      ? `box-shadow: 2px 2px 1px lightgrey;`
      : ''
  }
  
  ${(props) =>
    props.isGhosting
      ? 'opacity: 0.9; color: darkgrey;'
      : ''
  }

  /* needed for SelectionCount */
  position: relative;

  ${props => props.horizontal ? "" : "flex-grow: 1;"}
  height: fit-content;
  max-width: 350px;

  &:focus {
    outline: none;
    border: 2px solid green;
  }

  -webkit-user-select: none;  /* Chrome all / Safari all */
  -moz-user-select: none;     /* Firefox all */
  -ms-user-select: none;      /* IE 10+ */
  user-select: none;          /* Likely future */
`
const SelectionCount = styled.div`
  right: -20px;
  top: -20px;
  color: white;
  background: royalblue;
  border-radius: 50%;
  height: 35px;
  width: 35px;
  line-height: 35px;
  position: absolute;
  text-align: center;
  font-size: 15px;
  
`;

const UnselectableText = styled.p`
  -webkit-user-select: none;  /* Chrome all / Safari all */
  -moz-user-select: none;     /* Firefox all */
  -ms-user-select: none;      /* IE 10+ */
  user-select: none;          /* Likely future */      
`

const tooltipStyle = {
  fontSize: 13,
}

interface ICourseViewProps {
  droppableIndex: number
  course: CourseModel
  prerequisitesOfCurrentDraggable: Set<string>
  horizontal?: boolean
  isSelected: boolean
  isGhosting: boolean
  selectionCount: number

  // Multi select
  toggleSelection: (course: CourseModel) => void,
  toggleSelectionInGroup: (course: CourseModel) => void,
  getContextMenuItems: (course: CourseModel) => MenuItemProps[],

  // Region select
  registerDraggable: (courseModel: CourseModel, ref: React.MutableRefObject<HTMLDivElement | null | undefined>) => void,
  isRegionSelecting: boolean,
}

const CourseView = (props: ICourseViewProps) => {
  let termOffering = formatTermOffering(props.course.termOffering, props.course.allocatedTerm);
  let errorList: string[] = formatErrors(props.course.errors);

  const wasToggleInSelectionGroupKeyUsed = (event: MouseEvent | KeyboardEvent) => {
    const isUsingMac = navigator.platform.indexOf('Mac') >= 0;
    return isUsingMac ? event.metaKey : event.ctrlKey;
  };

  const performAction = (event: MouseEvent | KeyboardEvent) => {
    if (wasToggleInSelectionGroupKeyUsed(event)) {
      props.toggleSelectionInGroup(props.course);
      return;
    }

    props.toggleSelection(props.course);
  };

  const onKeyDown = (event: KeyboardEvent, provided: DraggableProvided, snapshot: DraggableStateSnapshot) => {
    if (event.defaultPrevented) return;

    if (snapshot.isDragging) return;

    if (event.key !== "enter") return;

    event.preventDefault();
    performAction(event);
  }

  const onClick = (event: MouseEvent) => {
    if (event.defaultPrevented) return;

    if (event.button !== 0) return;

    if (props.isRegionSelecting) return;

    event.preventDefault();
    if (contextOpen) return;

    performAction(event);
  };

  // Context menu
  const contextRef = React.useRef<any>();
  const [contextOpen, setContextOpen] = React.useState(false);

  const createContextFromEvent = (e) => {
    return {
      getBoundingClientRect: () => ({
        left: e.clientX,
        top: e.clientY,
        right: e.clientX + 1,
        bottom: e.clientY + 1,
        height: 0,
        width: 0,
      }),
    }
  }

  const onContextMenu = (event) => {
    event.preventDefault()

    contextRef.current = createContextFromEvent(event)
    setContextOpen(true)
  }

  // Drag region selection
  const draggableRef = React.useRef<HTMLDivElement | null | undefined>();
  props.registerDraggable(props.course, draggableRef);

  return (
    <Draggable draggableId={props.course.code} index={props.droppableIndex} >
      {((provided, snapshot) => {
        const shouldShowSelection: boolean = snapshot.isDragging && props.selectionCount > 1;
        return (
          <Container
            horizontal={props.horizontal}
            {...provided.draggableProps}
            {...provided.dragHandleProps}
            ref={(el) => {
              provided.innerRef(el);
              draggableRef.current = el;
            }}
            isDragging={snapshot.isDragging}
            isPrerequisiteOfCurrentDraggable={props.prerequisitesOfCurrentDraggable.has(props.course.id)}
            hasErrors={() => props.course.hasErrors()}
            isSelected={props.isSelected}
            onClick={onClick}
            onKeyDown={(event: KeyboardEvent) => onKeyDown(event, provided, snapshot)}
            isGhosting={props.isGhosting}
            onContextMenu={onContextMenu}
            data-draggable={true}
          >
            <Popup
              basic
              context={contextRef}
              onClose={() => setContextOpen(false)}
              open={contextOpen}
            >
              <Menu
                items={props.getContextMenuItems(props.course)}
                onItemClick={(e) => {
                  setContextOpen(false)
                }}
                secondary
                vertical
              />
            </Popup>

            {!props.isRegionSelecting ? (
              <Tooltip title={
                <span>
                  <UnselectableText style={tooltipStyle}>{termOffering}</UnselectableText>
                  <UnselectableText style={tooltipStyle}>{errorList[0]}</UnselectableText>
                  <UnselectableText style={tooltipStyle}>{errorList[1]}</UnselectableText>
                </span>}
                aria-label="add" aria-multiline="true" arrow={true}
              >
                <UnselectableText> {props.course.code} - {props.course.title}</UnselectableText>
              </Tooltip>
            ) : <UnselectableText> {props.course.code} - {props.course.title}</UnselectableText>}

            {shouldShowSelection ? (
              <SelectionCount>{props.selectionCount}</SelectionCount>
            ) : undefined}
          </Container>
        )
      })}
    </Draggable>
  )
}

function formatErrors(errors: IError[]): string[] {
  let output: string[] = [];
  let missing: string[] = [];
  let missingFormatted: string[] = [];
  const regex = /<br\/>/ig;

  if (errors.length === 0) {
    return [""];
  }

  if (errors.length === 1) {
    return [errors[0].message.replace(regex, ""), `${errors[0].missing}`];
  }

  for (let i = 0; i < errors.length; i++) {
    output.push(errors[i].message.replace(regex, ""));
    missing.push(errors[i].missing);
  }

  // Format missing arr for empty elements and remove from array before joining
  for (let i = 0; i < errors.length; i++) {
    if (missing[i] !== "") {
      missingFormatted.push(missing[i]);
    }
  }

  return [output.join(" & "), missingFormatted.join(" & ")];
}

function formatTermOffering(termOffering: string[], allocatedPeriod: TermModel | undefined) {
  termOffering = termOffering.slice().sort();
  let output: string = termOffering.join(", ");

  output = `Term Offerings: ${output}`;

  return output;
}

export default CourseView
