import { Observer, observer } from 'mobx-react';
import React, { useEffect, useCallback, useRef, useState } from 'react';
import styled from 'styled-components'
import { DragDropContext, DragStart, DropResult } from 'react-beautiful-dnd';
import { TermModel } from '../../models/TermModel';
import { PlannerViewModel } from './PlannerViewModel';
import CourseGroupView from './CourseGroupView/CourseGroupView';
import YearView from './YearView/YearView';
import { CourseModel, CourseStatus } from '../../models/CourseModel';
import { MenuItemProps } from 'semantic-ui-react';
import { Box, useSelectionContainer } from 'react-drag-to-select'

const Container = styled.div`
  display: flex;
  flex-direction: row;
`

const YearPlanner = styled.div`
  display: inherit;
  overflow: auto;
`

const Planner = observer(({ plannerViewModel }: { [key: string]: PlannerViewModel }) => {
  return <Observer>
    {() => {
      useEffect(() => {
        window.addEventListener("click", onWindowClick);
        window.addEventListener("keydown", onWindowKeyDown);

        return () => {
          window.removeEventListener("click", onWindowClick);
          window.removeEventListener("keydown", onWindowKeyDown);
        }
      });

      const courseState = plannerViewModel.courseState;
      const plannerState = plannerViewModel.plannerState;

      // Drag region selection
      const [isRegionSelecting, setIsRegionSelecting] = useState(false);
      const draggableItemRef = useRef<Map<CourseModel, HTMLDivElement>>(new Map<CourseModel, HTMLDivElement>());
      const linesRef = useRef<[CourseModel, [number, number, number][]][]>([]);

      const registerDraggable = (courseModel: CourseModel, ref: React.MutableRefObject<HTMLDivElement | null | undefined>) => {
        if (!ref.current) return;
        draggableItemRef.current?.set(courseModel, ref.current);
      }

      const isIntersectingHorizontal = (box: Box, y: number, x1: number, x2: number): boolean => {
        return (
          (box.top <= y && y <= box.top + box.height) &&
          (
            (x1 <= box.left && box.left <= x2) ||
            (x1 <= box.left + box.width && box.left + box.width <= x2)
          )
        )
      }

      const isIntersectingVertical = (box: Box, x: number, y1: number, y2: number): boolean => {
        return (
          (box.left <= x && x <= box.left + box.width) &&
          (
            (y1 <= box.top && box.top <= y2) ||
            (y1 <= box.top + box.height && box.top + box.height <= y2)
          )
        )
      }

      const isInsideBox = (box: Box, x: number, y: number, _) => {
        return (
          box.left <= x && x <= box.left + box.width &&
          box.top <= y && y <= box.top + box.height
        )
      }

      const handleSelectionChange = useCallback((box: Box) => {
        linesRef.current.reduce((previous: CourseModel[], [courseModel, lines]) => {
          const isSelected: boolean = plannerViewModel.selectedGroup.courses.includes(courseModel);
          const isIntersecting: boolean = (
            isIntersectingHorizontal(box, ...lines[0]) ||
            isIntersectingHorizontal(box, ...lines[1]) ||
            isIntersectingVertical(box, ...lines[2]) ||
            isIntersectingVertical(box, ...lines[3]) ||
            isInsideBox(box, ...lines[4])
          );

          if (isSelected !== isIntersecting) return [...previous, courseModel];
          else return previous;
        }, []).forEach(toggleSelectionInGroup);
      }, []);

      const { DragSelection } = useSelectionContainer({
        onSelectionStart: () => {
          setIsRegionSelecting(true);
          linesRef.current = [...draggableItemRef.current.entries()].reduce((previous: [CourseModel, [number, number, number][]][], [courseModel, div]) => {
            const rect = div.getBoundingClientRect();
            return [
              ...previous,
              [courseModel, [
                [rect.top, rect.left, rect.right],
                [rect.bottom, rect.left, rect.right],
                [rect.left, rect.top, rect.bottom],
                [rect.right, rect.top, rect.bottom],
                [rect.x, rect.y, 0]
              ]]
            ];
          }, []);
        },
        onSelectionChange: handleSelectionChange,
        onSelectionEnd: () => {
          linesRef.current = [];
        }
      });

      // Dragging
      const onDragEnd = (result: DropResult) => {
        const { destination } = result;
        if (!destination || result.reason === "CANCEL") {
          plannerViewModel.draggingCourseId = undefined;
          return;
        }
        if (!plannerViewModel.draggingCourseId) return;

        const setCourse = (courseModel: CourseModel) => {
          if (destination.droppableId === "trash") {
            courseModel.setUnplanned()
            draggableItemRef.current.delete(courseModel);
          } else if (destination.droppableId === "completed") {
            courseModel.setCompleted()
          } else if (destination.droppableId === "planned") {
            courseModel.setPlanned()
          } else {
            courseModel.setAllocated(TermModel.fromMappingName(destination.droppableId))
          }
        }

        plannerViewModel.selectedGroup.courses.forEach(setCourse);

        const draggingCourseIsSelected: boolean = !!plannerViewModel.selectedGroup.courses.find((courseModel) => {
          return courseModel.id === plannerViewModel.draggingCourseId;
        });
        if (!draggingCourseIsSelected) {
          const courseModel = courseState.getCourseById(plannerViewModel.draggingCourseId);
          if (courseModel) setCourse(courseModel);
        }

        plannerViewModel.draggingCourseId = undefined;

        plannerViewModel.synchronizePlannerState()
        plannerViewModel.clearCurrentDraggableProps()
      }

      const onBeforeCapture = () => {
        plannerViewModel.currentDraggableProps = {
          ...plannerViewModel.currentDraggableProps,
          isDragging: true,
        }
      }

      const onDragStart = (initial: DragStart) => {
        const selected: CourseModel | undefined = plannerViewModel.selectedGroup.courses.find(
          (course: CourseModel): boolean => course.id === initial.draggableId,
        );

        if (!selected) unselectAll();

        plannerViewModel.draggingCourseId = initial.draggableId;

        const course = courseState.getCourseById(initial.draggableId);

        if (!course) return;

        plannerViewModel.currentDraggableProps = {
          ...plannerViewModel.currentDraggableProps,
          possibleTerms: plannerViewModel.calculateDisplayableTerms().filter(term => {
            return courseState.getMissingRequirementsWhenAssigningCourseToTerm(course, term).length === 0
          }),
          prerequisiteCourseIds: new Set<string>(courseState.getFlattenedPrerequisiteList(course.prerequisites))
        }
      }

      // Multi select
      const unselectAll = () => {
        plannerViewModel.selectedGroup = {
          ...plannerViewModel.selectedGroup,
          courses: [],
        };
      }

      const onWindowKeyDown = (event: KeyboardEvent) => {
        if (event.defaultPrevented) return;

        if (event.key === "Escape") unselectAll();
      }

      const onWindowClick = (event: MouseEvent) => {
        if (event.defaultPrevented) return;

        if (isRegionSelecting) {
          setIsRegionSelecting(false);
          return;
        }

        if (event.button !== 0) return;
        if (event.ctrlKey) return;

        unselectAll();
      }

      const toggleSelection = (course: CourseModel) => {
        const selectedCourses = plannerViewModel.selectedGroup;
        const wasSelected: boolean = selectedCourses.courses.includes(course);

        const newCourses = (() => {
          // Task not previously selected or part of the selected group, now the only selected item
          if (!wasSelected || selectedCourses.courses.length > 1) {
            return [course];
          }

          // Task previously selected but not in a group, clear the selection
          return [];
        })();

        plannerViewModel.selectedGroup = {
          ...plannerViewModel.selectedGroup,
          courses: newCourses
        };
      }

      const toggleSelectionInGroup = (course: CourseModel) => {
        const selectedCourses = plannerViewModel.selectedGroup;
        const index: number = selectedCourses.courses.indexOf(course);

        // if not selected - add it to the selected items
        if (index === -1) {
          plannerViewModel.selectedGroup = {
            ...plannerViewModel.selectedGroup,
            courses: [...selectedCourses.courses, course]
          };
          return;
        }

        // it was previously selected and now needs to be removed from the group
        const newCourses: CourseModel[] = [...selectedCourses.courses];
        newCourses.splice(index, 1);
        plannerViewModel.selectedGroup = {
          ...plannerViewModel.selectedGroup,
          courses: newCourses,
        };
      };

      // Context Menu
      const contextMenuItems = (course: CourseModel) => {
        const moveCourseToRemoved = () => {
          if (course.courseStatus !== CourseStatus.UNPLANNED) {
            course.setUnplanned();
            draggableItemRef.current.delete(course);
          }

          if (plannerViewModel.selectedGroup.courses.includes(course)) {
            plannerViewModel.selectedGroup.courses.filter((courseModel: CourseModel) => {
              return courseModel.courseStatus !== CourseStatus.UNPLANNED;
            }).forEach((courseModel: CourseModel) => {
              courseModel.setUnplanned();
              draggableItemRef.current.delete(courseModel);
            });
            plannerViewModel.selectedGroup = {
              ...plannerViewModel.selectedGroup,
              courses: [],
            };
          }

          plannerViewModel.synchronizePlannerState();
        }

        const moveCourseToPlanned = () => {
          if (course.courseStatus !== CourseStatus.PLANNED) {
            course.setPlanned();
          }

          if (plannerViewModel.selectedGroup.courses.includes(course)) {
            plannerViewModel.selectedGroup.courses.filter((courseModel: CourseModel) =>
              courseModel.courseStatus !== CourseStatus.PLANNED
            ).forEach(courseModel => courseModel.setPlanned());
          }

          plannerViewModel.synchronizePlannerState();
        }

        const moveCourseToCompleted = () => {
          if (course.courseStatus !== CourseStatus.COMPLETED) {
            course.setCompleted();
          }

          if (plannerViewModel.selectedGroup.courses.includes(course)) {
            plannerViewModel.selectedGroup.courses.filter((courseModel: CourseModel) =>
              courseModel.courseStatus !== CourseStatus.COMPLETED
            ).forEach(courseModel => courseModel.setCompleted());
          }

          plannerViewModel.synchronizePlannerState();
        }

        let menuItems: MenuItemProps[] = [
          { key: 'remove', content: 'Remove course', icon: 'delete calendar', onClick: () => { moveCourseToRemoved() } },
          { key: 'plan', content: 'Move to planned', icon: 'bookmark', onClick: () => moveCourseToPlanned() },
          { key: 'complete', content: 'Move to completed', icon: 'checked calendar', onClick: () => moveCourseToCompleted() },
        ];
        if (course.courseStatus === CourseStatus.UNPLANNED) {
          menuItems.splice(0, 1);
        } else if (course.courseStatus === CourseStatus.PLANNED) {
          menuItems.splice(1, 1);
        } else if (course.courseStatus === CourseStatus.COMPLETED) {
          menuItems.splice(2, 1);
        }
        return menuItems;
      }

      return (
        <React.Fragment>
          <DragSelection />
          <DragDropContext
            onBeforeCapture={onBeforeCapture}
            onDragStart={onDragStart}
            onDragEnd={onDragEnd}
          >
            {
              <CourseGroupView
                horizontal={true}
                key={plannerState.completedGroup.id}
                id={plannerState.completedGroup.id}
                title={plannerState.completedGroup.title}
                term={null}
                courses={plannerState.completedGroup.courses}
                currentDraggableProps={plannerViewModel.currentDraggableProps}
                selectedCourses={plannerViewModel.selectedGroup}
                draggingCourseId={plannerViewModel.draggingCourseId}
                toggleSelection={toggleSelection}
                toggleSelectionInGroup={toggleSelectionInGroup}
                getContextMenuItems={contextMenuItems}
                registerDraggable={registerDraggable}
                isRegionSelecting={isRegionSelecting}
              />
            }
            <Container>
              {
                <CourseGroupView
                  horizontal={false}
                  key={plannerState.plannedGroup.id}
                  id={plannerState.plannedGroup.id}
                  title={plannerState.plannedGroup.title}
                  term={null}
                  courses={plannerState.plannedGroup.courses}
                  currentDraggableProps={plannerViewModel.currentDraggableProps}
                  selectedCourses={plannerViewModel.selectedGroup}
                  draggingCourseId={plannerViewModel.draggingCourseId}
                  toggleSelection={toggleSelection}
                  toggleSelectionInGroup={toggleSelectionInGroup}
                  getContextMenuItems={contextMenuItems}
                  registerDraggable={registerDraggable}
                  isRegionSelecting={isRegionSelecting}
                />
              }
              <YearPlanner>
                {
                  plannerState.yearGroups.map(yearGroup => {
                    return <YearView
                      id={yearGroup.id}
                      title={yearGroup.title}
                      year={yearGroup.year}
                      courseGroups={yearGroup.courseGroups}
                      currentDraggableProps={plannerViewModel.currentDraggableProps}
                      selectedCourses={plannerViewModel.selectedGroup}
                      draggingCourseId={plannerViewModel.draggingCourseId}
                      toggleSelection={toggleSelection}
                      toggleSelectionInGroup={toggleSelectionInGroup}
                      getContextMenuItems={contextMenuItems}
                      registerDraggable={registerDraggable}
                      isRegionSelecting={isRegionSelecting}
                    />
                  })
                }
              </YearPlanner>
            </Container>
            {
              plannerViewModel.currentDraggableProps.isDragging
                ?
                <CourseGroupView
                  horizontal={true}
                  key={"trash"}
                  id={"trash"}
                  title={"Remove"}
                  term={null}
                  courses={[]}
                  currentDraggableProps={plannerViewModel.currentDraggableProps}
                  selectedCourses={plannerViewModel.selectedGroup}
                  draggingCourseId={plannerViewModel.draggingCourseId}
                  toggleSelection={toggleSelection}
                  toggleSelectionInGroup={toggleSelectionInGroup}
                  getContextMenuItems={contextMenuItems}
                  registerDraggable={registerDraggable}
                  isRegionSelecting={isRegionSelecting}
                />
                : <></>
            }
          </DragDropContext>
        </React.Fragment>
      )
    }}
  </Observer >
})

export default Planner;
