import { MenuItemProps } from 'semantic-ui-react';
import styled from 'styled-components';
import { CourseModel } from '../../../models/CourseModel';
import CourseGroupView from '../CourseGroupView/CourseGroupView';
import { ICourseGroup, ICurrentDraggableProps } from '../PlannerViewModel';

const Container = styled.div`
    margin: 8px;
    border: 1px solid lightgrey;
    border-radius:2px;
    width: 250px;
    -webkit-user-select: none;  /* Chrome all / Safari all */
    -moz-user-select: none;     /* Firefox all */
    -ms-user-select: none;      /* IE 10+ */
    user-select: none;          /* Likely future */ 
`

const Title = styled.h3`
    padding: 6px;
    margin: 3px;
    display: flex;
    width: 200px;
`;

interface IYearViewProps {
  id: string,
  title: string,
  year: number,
  courseGroups: ICourseGroup[],
  currentDraggableProps: ICurrentDraggableProps,
  selectedCourses: ICourseGroup,
  draggingCourseId?: string,

  // Multi select
  toggleSelection: (course: CourseModel) => void,
  toggleSelectionInGroup: (course: CourseModel) => void,
  getContextMenuItems: (course: CourseModel) => MenuItemProps[]

  // Region select
  registerDraggable: (courseModel: CourseModel, ref: React.MutableRefObject<HTMLDivElement | null | undefined>) => void,
  isRegionSelecting: boolean,
}

const YearView = (props: IYearViewProps) => {
  return (
    <Container>
      <Title>{props.year}</Title>
      {
        props.courseGroups.map(courseGroup => {
          // console.log(`Rendering term ${courseGroup.title} with courses ${courseGroup.courses}`)
          return (
            <CourseGroupView
              key={courseGroup.id}
              id={courseGroup.id}
              title={courseGroup.title}
              courses={courseGroup.courses}
              term={courseGroup.term}
              currentDraggableProps={props.currentDraggableProps}
              selectedCourses={props.selectedCourses}
              draggingCourseId={props.draggingCourseId}
              toggleSelection={props.toggleSelection}
              toggleSelectionInGroup={props.toggleSelectionInGroup}
              getContextMenuItems={props.getContextMenuItems}
              registerDraggable={props.registerDraggable}
              isRegionSelecting={props.isRegionSelecting}
            />
          )
        })
      }
    </Container>
  )
};
export default YearView
